raul-container-loader {
  display: block;
  font-size: 0.875rem;
}

raul-container-loader .r-container-loader__container {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 50;
  background-color: rgba(255, 255, 255, 0.8);
}

raul-container-loader .r-container-loader__container .r-container-loader__dots-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  opacity: 1;
  width: 3rem;
}

raul-container-loader .r-container-loader__container .r-container-loader__dot {
  background-color: #C6CCD0;
  border-radius: 9999px;
  -webkit-animation: color-change 1s infinite linear;
  animation: color-change 1s infinite linear;
  height: 10px;
  width: 10px;
}

@-webkit-keyframes color-change {
  0% {
    background-color: #0076CC;
  }

  20% {
    background-color: #0076CC;
  }

  50% {
    background-color: #C6CCD0;
  }

  100% {
    background-color: #C6CCD0;
  }
}

@keyframes color-change {
  0% {
    background-color: #0076CC;
  }

  20% {
    background-color: #0076CC;
  }

  50% {
    background-color: #C6CCD0;
  }

  100% {
    background-color: #C6CCD0;
  }
}