raul-checkbox {
  display: inline-block;
  vertical-align: middle;
  font-size: 0.875rem;
}

raul-checkbox .r-checkbox__label {
  cursor: pointer;
  display: flex;
  align-items: center;
  position: relative;
}

raul-checkbox .r-checkbox__label input[type=checkbox] {
  position: absolute;
  height: 0;
  opacity: 0;
  width: 0;
  z-index: -10;
}

raul-checkbox .r-checkbox__label input[type=checkbox]:checked ~ .r-checkbox__label__icon, raul-checkbox .r-checkbox__label input[type=checkbox]:indeterminate ~ .r-checkbox__label__icon {
  background-color: #0076CC;
  border-color: #0076CC;
}

raul-checkbox .r-checkbox__label input[type=checkbox]:checked ~ .r-checkbox__label__icon::after {
  position: absolute;
  border-left-width: 2px;
  border-top-width: 2px;
  border-color: #fff;
  content: "";
  height: 13px;
  left: 7px;
  top: 14px;
  transform: rotate(-135deg);
  transform-origin: 0 0;
  width: 7px;
}

raul-checkbox .r-checkbox__label input[type=checkbox]:indeterminate ~ .r-checkbox__label__icon::after {
  position: absolute;
  border-top-width: 2px;
  border-color: #fff;
  left: 50%;
  top: 50%;
  content: "";
  transform: translate(-50%, -50%);
  width: 10px;
}

raul-checkbox .r-checkbox__label input[type=checkbox]:disabled ~ .r-checkbox__label__icon {
  background-color: #EBEDEE;
  border-color: #EBEDEE;
}

raul-checkbox .r-checkbox__label input[type=checkbox]:disabled:checked ~ .r-checkbox__label__icon::after, raul-checkbox .r-checkbox__label input[type=checkbox]:disabled:indeterminate ~ .r-checkbox__label__icon::after {
  border-color: #9BA3A7;
}

body[modality] raul-checkbox .r-checkbox__label input[type=checkbox]:focus ~ .r-checkbox__label__icon::before {
  position: absolute;
  border-radius: 0.125rem;
  top: -4px;
  right: -4px;
  bottom: -4px;
  left: -4px;
  box-shadow: 0 0 0 1px #0076CC;
  content: "";
}

raul-checkbox .r-checkbox__label__icon {
  background-color: #fff;
  border-width: 1px;
  border-color: #C6CCD0;
  flex: none;
  position: relative;
  border-radius: 0.125rem;
  height: 20px;
  transition: background-color 0.15s linear, border-color 0.15s linear;
  width: 20px;
}

raul-checkbox .r-checkbox__label__text {
  font-size: 0.875rem;
  margin-left: 0.5rem;
  word-break: break-all;
}

raul-checkbox .r-checkbox--small .r-checkbox__label__icon {
  height: 14px;
  width: 14px;
}

raul-checkbox .r-checkbox--small .r-checkbox__label input[type=checkbox]:checked ~ .r-checkbox__label__icon::after {
  height: 10px;
  width: 5px;
  left: 4px;
  top: 10px;
}

raul-checkbox .r-checkbox--small .r-checkbox__label input[type=checkbox]:indeterminate ~ .r-checkbox__label__icon::after {
  width: 7px;
}

raul-checkbox .r-checkbox--invalid .r-checkbox__label__icon {
  border-width: 1px;
  border-color: #D01A1F;
}